.sticky {
	display: block;
}

.hentry {
	margin: 0 0 3rem;
}

.entry-title {
	margin: 10px auto 5px;
	color: $gradient-color1;
	
	> a {
		padding: 5px;
	}
	> a:hover,
	> a:active,
	> a:focus {
		border-top: 1px solid $color__ambers-purple;
		font-weight: bold;
		color: $color__ambers-purple;
	}
}

.entry-header > .entry-meta {
	> .posted-on a:hover,
	> .posted-on a:active,
	> .posted-on a:focus,
	> .byline a:hover,
	> .byline a:active,
	> .byline a:focus {
		border-bottom: 1px solid $color__ambers-purple;
		font-weight: bold;
		color: $color__ambers-purple;
	}
}

.entry-header hr {
	border: 0;
    height: 2px;
    background-image: linear-gradient(to right, $gradient-color1, $gradient-color2, $gradient-color1);
}

.byline,
.updated:not(.published){
	display: none;
}

.posted-on {
	@include font-size(0.869);
	float: right;
}

.posted-on,
.byline {
	display: block;
}

.entry-header {
	margin: 0 1.6rem;
}

.entry-meta .byline {
	@include font-size(0.869);
}

.entry-content {
	margin: 1.5rem;
}

.entry-content .more-link {
	float: right;
}

.page-content,
.entry-summary {
	margin: 1.5rem 0 0;
}

.entry-footer {
	margin: 1.5rem;
	text-align: center;
}

.entry-footer .comments-link,
.entry-footer .post-edit-link {
	// padding: 1.5rem 1.5rem 1.5rem 0.543rem;
	font-family: $font__main;
	letter-spacing: initial;
	font-weight: normal;
	display: inline;
	> a:hover,
	> a:active,
	> a:focus {
		border-bottom: 1px solid $color__ambers-purple;
		font-weight: bold;
		color: $color__ambers-purple;
	}
}

.edit-link {
	@include font-size(0.978);

	&:before {
		content: '// ';
		@include font-size(.92);
		letter-spacing: 0.07rem;
		width: 10px;
		height: 10px;
	}

	> a:hover,
	> a:active,
	> a:focus {
		border-bottom: 1px solid $color__ambers-purple;
		font-weight: bold;
		color: $color__ambers-purple;
	}
}

.page-links {
	clear: both;
	margin: 0 0 1.5rem;
}

nav .nav-links {
	> .nav-previous,
	> .nav-next {
		margin: 0 1.5rem 0 1.5rem;
		width: 35%;
	}
}

.site-footer {
	padding: 0.869rem 1.5rem;

	.site-info {
		@include font-size(0.687);
		text-align: center;

		> .sep {
			display: none;
		}

		> .wp-credit,
		> .theme-credit {
			display: inline-block;
		}
		
		/*  350 / 16 = 21.875  */
		@media screen and (min-width: 21.875em) {
			> .sep {
				display: inline;
			}
		}

		/*  400 / 16 = 25  */
		@media screen and (min-width: 25em) {
			text-align: center;
			@include font-size(0.773);

			> .wp-credit,
			> .theme-credit {
				display: inline;
			}
		}

		/*  800 / 16 = 50  */
		@media screen and (min-width: 50em) {
			@include font-size(0.869);
		}
	}
}

.site-main .error-404 {
	margin: 0 1.5rem;

	> .page-content > .search-form {
		margin: 0 auto 30px;
		width: 265px;

		// 600 / 16 = 37.5
		@media screen and (min-width: 37.5em) {
			margin: 0 0 30px;
			width: auto;
		}
	}

	> .page-content > img {
		border-radius: 50%;
		display: block;
		margin: 0 auto;
		padding-bottom: 20px;
	}
}



